import { Component, Prop, State, h } from '@stencil/core';
import { decode } from 'blurhash';

@Component({
  tag: 'ir-image',
  styleUrl: 'ir-image.css',
  shadow: true,
})
export class IrImage {
  @Prop() src: string;
  @Prop() thumbnail: string;
  @Prop() blurhash: string;
  @Prop() width: number = 32;
  @Prop() height: number = 32;
  @Prop() alt: string;

  @State() blurDataUrl: string;
  @State() loaded: boolean = false;

  componentWillLoad() {
    this.decodeBlurHash();
  }

  decodeBlurHash() {
    if (this.blurhash) {
      const pixels = decode(this.blurhash, this.width, this.height);
      const canvas = document.createElement('canvas');
      canvas.width = this.width;
      canvas.height = this.height;

      const ctx = canvas.getContext('2d');
      const imageData = new ImageData(new Uint8ClampedArray(pixels), this.width, this.height);
      ctx.putImageData(imageData, 0, 0);

      this.blurDataUrl = canvas.toDataURL();
    }
  }

  handleImageLoad() {
    this.loaded = true;
  }

  render() {
    return (
      <div class="image-container">
        {this.blurDataUrl && !this.thumbnail && <img src={this.blurDataUrl} class={`placeholder ${this.loaded ? 'hidden' : ''}`} alt="placeholder" />}
        {this.thumbnail !== undefined && <img src={`data:image/png;base64,${this.thumbnail}`} class={`placeholder ${this.loaded ? 'hidden' : ''}`} alt="placeholder" />}
        <img src={this.src} class={`original ${this.loaded ? 'visible' : ''}`} alt={this.alt} loading="lazy" onLoad={() => this.handleImageLoad()} />
      </div>
    );
  }
}
